* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

body {
  color: #333;
  background-color: rgb(250, 255, 176);
}

.list {
  max-width: 640px;
  width: calc(100% - 20px);
  height: 500px;
  margin: 100px auto;
  padding-top: 50px;
  background-color: #FFF;
  text-align: center;
  border-radius: 20px;
}

.list h2 {
  margin-bottom: 20px;
  font-size: 40px;
}

.list form input {
  width: 400px;
  height: 40px;
  border: 2px solid #fcc01c;
  border-radius: 10px;
}

.list form button {
  margin-left: 10px;
  height: 40px;
  width: 50px;
  background-color: #fff;
  border-radius: 10px;
  border: 2px solid #fcc01c;
  cursor: pointer;
}

.list ul {
  margin-top: 20px;
  padding-left: 90px;
  text-align: left;
}

.list ul li {
  margin-bottom: 5px;
}

.list ul li:nth-child(odd)::before {
  content: '😝';
  position: relative;
  left: -5px;
  top: 0;
}

.list ul li:nth-child(even)::before {
  content: '💖';
  position: relative;
  left: -5px;
  top: 0;
}

.list ul li:nth-child(3n)::before {
  content: '🐰';
  position: relative;
  left: -5px;
  top: 0;
}


/* nth-child(2n) =>2배수
nth-child(odd) =>홀수
nth-child(even) =>짝수 
 */


